:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --dkblack: hsl(0, 0%, 0%);
  --black: hsl(0, 0%, 11%);
  --ltblack: hsl(0, 0%, 21%);
  --white: hsl(0, 0%, 100%);
  --dkwhite: hsl(0, 8%, 71%);
  --gray: hsl(0, 0%, 49%);
  --yellow: hsl(60, 100%, 50%);
  --blue: hsl(240, 100%, 50%);
  --red: hsl(0, 100%, 50%);
  --orange: hsl(39, 100%, 50%);
  --coolorange: hsl(0, 82%, 73%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -625rem;
  top: auto;
  width: 0.0625rem;
  height: 0.0625rem;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-weight: bolder;
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: var(--white);
}

br {
  margin-top: 0.3125rem;
  margin-bottom: 0rem;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 6.25rem;
  font-size: 1.0625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 3;
  }
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  display: flex;
  justify-content: center;
  margin-top: 0.9375rem;
}
.header__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 13.125rem;
  padding: 0.375rem;
  border: 0 solid;
  background-color: var(--gray);
  border-radius: 1.5rem;
  opacity: 0.85;
}
.header__button a {
  background-color: var(--black);
  font-weight: 700;
  color: var(--white);
  margin: 0.0625rem 0.06875rem;
  cursor: pointer;
  border-radius: 1rem;
  width: 6.25rem;
  height: 2.1875rem;
  box-shadow: 0 0.625rem 1.5625rem rgba(0, 0, 0, 0.5);
  transition: opacity 200ms ease-in-out;
}
.header__button a:hover {
  background-color: var(--ltblack);
}
.header__button:hover {
  opacity: 0.82;
}
.header__central1 {
  padding: 0.375rem 1.1875rem;
}
.header__central2 {
  padding: 0.375rem 1.875rem;
}

.no-scroll {
  position: fixed;
}

body.menu-open .sliders {
  display: none;
}

.topnav {
  display: flex;
  justify-content: space-between; /* Align items to the right */
  align-items: center;
  padding-block: 1.5rem;
}
.topnav button {
  background: none;
  border: none;
  padding: 0;
}
.topnav__blockm {
  position: absolute;
  right: 2rem;
  width: 5.9375rem;
  height: 3.9375rem;
  border-radius: 2.25rem;
  background-color: var(--dkblack);
  cursor: pointer;
}
@media (max-width: 30em) {
  .topnav__blockm {
    border-radius: 1.25rem;
    right: 1rem;
    width: 3.938rem;
    height: 3.938rem;
  }
}
.topnav__open[aria-expanded=true] + .topnav__menu {
  translate: 0;
  opacity: 1;
}
.topnav__close {
  display: block;
  margin-inline-start: auto;
  margin-block-end: 8rem;
  cursor: pointer;
  background: radial-gradient(circle, var(--white), var(--red));
}
@media (max-width: 28.875em) {
  .topnav__close {
    position: relative;
    right: -0.6875rem;
  }
}
.topnav__close img {
  width: 2.5rem;
}
.topnav__menu {
  position: fixed;
  inset: 0;
  padding: 1.5rem;
  background-color: var(--dkblack);
  translate: 100vw 0;
  transition: translate 0.5s ease-in-out;
  background: radial-gradient(circle, var(--black), var(--white));
  translate: 0;
  opacity: 0;
  transition: opacity 250ms ease-in-out;
  z-index: 1000; /* Ensure it's on top of other elements */
}
.topnav__links {
  display: flex;
  align-items: center;
  margin: 0;
  flex-direction: column;
}
.topnav__item {
  list-style-type: none;
  position: relative;
}
.topnav__item .topnav__link {
  position: relative;
  display: inline-block;
  color: var(--white); /* Text color */
  padding: 0.5rem 1rem; /* Adjust padding as needed */
  cursor: pointer; /* Change cursor on hover */
  background: var(--black);
  color: var(--white);
  font-weight: bold;
  font-size: 3.75rem;
  border-radius: 5.875rem;
}
.topnav__item .topnav__link:hover {
  background-color: var(--gray); /* Change background color on hover */
  color: var(--black);
}

.body {
  background: linear-gradient(var(--white), var(--gray), var(--white)), no-repeat center center fixed;
  background-size: cover;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  background-color: var(--dkwhite);
  border-radius: 2rem;
  margin-top: 6rem;
  width: 41.75rem;
}
@media (max-width: 43em) {
  .cards {
    width: 20.75rem;
  }
}
.cards__title {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bolder;
}
.cards p {
  font-size: 1.25rem;
  line-height: 1.5;
  font-weight: bold;
}
.cards:hover {
  background-color: var(--gray);
}

.card2 {
  position: relative;
  top: 13.4375rem;
}

* {
  padding: 0;
  box-sizing: border-box;
}

.center {
  display: flex;
  text-align: center;
  position: relative;
  perspective: 75rem;
  width: 46.75rem;
  left: -13.3rem;
}
@media (min-width: 100em) {
  .center {
    left: -6.25rem;
  }
}
@media (min-width: 53em) {
  .center {
    left: 2.375rem;
    bottom: -6.4375rem;
  }
}
@media (max-width: 38.25em) {
  .center {
    justify-content: center;
    text-align-last: center;
    max-width: 19.5625rem;
    margin-left: 1.875rem;
  }
}

.center .img-card {
  position: relative;
  height: 21.875rem;
  width: 25rem;
  transform-style: preserve-3d;
  transition: transform 0.5s ease-in;
}

.img-card img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.img-card img:nth-child(1) {
  transform: rotateX(-270deg) translateY(-10.9375rem);
  transform-origin: top left;
}

.img-card img:nth-child(2) {
  transform: translateZ(10.9375rem);
}

.img-card img:nth-child(3) {
  transform: rotateX(-90deg) translateY(10.9375rem);
  transform-origin: bottom center;
}

.img-card img:nth-child(4) {
  transform: rotateX(-180deg) translateY(21.875rem) translateZ(10.9375rem);
  transform-origin: bottom right;
}

#tab-1:checked ~ .img-card {
  transform: rotateX(-90deg);
}

#tab-2:checked ~ .img-card {
  transform: rotateX(0deg);
}

#tab-3:checked ~ .img-card {
  transform: rotateX(90deg);
}

#tab-4:checked ~ .img-card {
  transform: rotateX(180deg);
}

.sliders {
  position: absolute;
  display: block;
  right: 0;
  top: -1.875rem;
}
@media (max-width: 40em) {
  .sliders {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.75rem;
    top: 24.25rem;
    right: 0;
    left: 81%;
    transform: translateX(-50%);
  }
}

.sliders label {
  height: 6.125rem;
  width: 6.875rem;
  display: flex;
  overflow: hidden;
  cursor: pointer;
  margin: 0.375rem 0;
  border: 0.125rem solid #2d4052;
}

@media (min-width: 40em) {
  .sliders label:nth-child(1) {
    margin-top: 0;
  }
}

label img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(1);
  opacity: 0.9;
}

label img:hover,
#tab-1:checked ~ .sliders label:nth-child(1) img,
#tab-2:checked ~ .sliders label:nth-child(2) img,
#tab-3:checked ~ .sliders label:nth-child(3) img,
#tab-4:checked ~ .sliders label:nth-child(4) img {
  filter: grayscale(0);
  opacity: 1;
}

input {
  display: none;
}

.footer hr {
  border: 0.125rem solid var(--dkblack);
}
.footer p {
  line-height: 1.3;
  font-weight: bold;
}
.footer a {
  display: grid;
  text-align: center;
  background-color: var(--black);
  border-radius: 14%;
  height: 1.25rem;
}
.footer__1 {
  position: relative;
  bottom: -15.4375rem;
  height: 21vh;
}
.footer #update-date {
  font-size: 0.875rem;
  font-weight: bold;
}

.membrii__title {
  display: flex;
  justify-content: end;
  font-size: 2.5625rem;
  font-weight: 700;
  margin-right: 0.625rem;
  margin-top: 0.375rem;
}
.membrii img {
  border-radius: 28%;
  display: flex;
  justify-self: right;
  height: 18.75rem;
  width: 18.75rem;
}
@media (max-width: 40em) {
  .membrii img {
    display: flex;
    justify-content: center;
  }
}
.membrii__h1 {
  color: var(--black);
  display: flex;
  justify-content: center;
  font-size: 2.5625rem;
}
@media (min-width: 43em) {
  .membrii .s {
    position: relative;
    left: 24.1875rem;
  }
}
.membrii .s a,
.membrii .s img {
  display: inline-block;
  vertical-align: middle;
}
.membrii .s img {
  max-width: 3.75rem;
  max-height: 3.75rem;
}
@media (max-width: 43em) {
  .membrii .s img {
    margin-left: 3.125rem;
  }
}
.membrii .s a {
  white-space: nowrap;
  font-weight: 700;
  font-size: 1.25rem;
}